import React, { useState } from "react"
import img1 from "../images/15.png"
import img2 from "../images/Svea.png"
import img3 from "../images/Zahita.jpg"
import img4 from "../images/Stroi-mat.jpg"
import img5 from "../images/snapedit_1699681524244.png"
import img6 from "../images/Apple.png"
import img7 from "../images/Variant2.png"
import img8 from "../images/Horses.jpg"
import img9 from "../images/Fish.png"
import img10 from "../images/Mal3.jpg"
import img11 from "../images/peugeot-partner-load-01.jpg"
import img12 from "../images/Maybach4-scaled.jpg"
import img13 from "../images/Rectangle 37.png"
import img14 from "../images/Sport-tovar.jpg"
import img15 from "../images/1674910661_top-fon-com-p-fon-dlya-prezentatsii-po-kinoiskusstvu-118.jpg"


import "../Style/newStyle.css"

const Directions = () => {
  const [activeMovieId1, setActiveMovieId1] = useState(null)
  const [activeMovieId2, setActiveMovieId2] = useState(null)
  const [activeMovieId3, setActiveMovieId3] = useState(null)
  const [activeMovieId4, setActiveMovieId4] = useState(null)

  const movies = [
    { id: 1, title: "Фильм 1", image: img1 },
    { id: 2, title: "Фильм 2", image: img2 },
    { id: 3, title: "Фильм 3", image: img3 },
    { id: 4, title: "Фильм 4", image: img4 },
  ]
  const movies2 = [
    { id: 1, title: "Фильм 1", image: img5 },
    { id: 2, title: "Фильм 2", image: img6 },
    { id: 3, title: "Фильм 3", image: img7 },
    { id: 4, title: "Фильм 4", image: img8 },
  ]
  const movies3 = [
    { id: 1, title: "Фильм 1", image: img9 },
    { id: 2, title: "Фильм 2", image: img10 },
    { id: 3, title: "Фильм 3", image: img11 },
    { id: 4, title: "Фильм 4", image: img12 },
  ]
  const movies4 = [
    { id: 1, title: "Фильм 1", image: img13 },
    { id: 2, title: "Фильм 2", image: img14 },
    { id: 3, title: "Фильм 2", image: img15 },
  ]

  const handleMovieClick1 = movieId => {
    if (activeMovieId1) {
      setActiveMovieId1(null)
    }
    setActiveMovieId1(activeMovieId1 === movieId ? null : movieId)
  }

  const handleMovieClick2 = movieId => {
    if (activeMovieId2) {
      setActiveMovieId2(null)
    }
    setActiveMovieId2(activeMovieId2 === movieId ? null : movieId)
  }

  const handleMovieClick3 = movieId => {
    if (activeMovieId3) {
      setActiveMovieId3(null)
    }
    setActiveMovieId3(activeMovieId3 === movieId ? null : movieId)
  }
  const handleMovieClick4 = movieId => {
    if (activeMovieId4) {
      setActiveMovieId4(null)
    }
    setActiveMovieId4(activeMovieId4 === movieId ? null : movieId)
  }


  return (
    <div className="newcenter">
      {/* Первый блок */}
      <div className="gridNew">
        {movies.map(movie => (
          <div
            className="newBlok"
            key={movie.id}
            onClick={() => handleMovieClick1(movie.id)}
            style={{ cursor: "pointer", padding: "5px", margin: "0px" }}
          >
            <img
              src={movie.image}
              alt={movie.title}
              style={{
                maxWidth: "300px",
                maxHeight: "300px",
                border: activeMovieId1 === movie.id ? "2px solid blue" : "none",
              }}
            />
          </div>
        ))}
      </div>
      {activeMovieId1 && (
        <div>
          <h2>{movies.find(movie => movie.id === activeMovieId1)?.title}</h2>
          <img className="minHeight"
            src={movies.find(movie => movie.id === activeMovieId1)?.image}
            alt={movies.find(movie => movie.id === activeMovieId1)?.title}
          />
        </div>
      )}

      {/* Второй блок */}
      <div className="gridNew">
        {movies2.map(movie2 => (
          <div
            className="newBlok"
            key={movie2.id}
            onClick={() => handleMovieClick2(movie2.id)}
            style={{ cursor: "pointer", padding: "5px", margin: "0px" }}
          >
            <img
              src={movie2.image}
              alt={movie2.title}
              style={{
                maxWidth: "300px",
                maxHeight: "300px",
                border:
                  activeMovieId2 === movie2.id ? "2px solid blue" : "none",
              }}
            />
          </div>
        ))}
      </div>
      {activeMovieId2 && (
        <div>
          <h2>{movies2.find(movie => movie.id === activeMovieId2)?.title}</h2>
          <img className="minHeight"
            src={movies2.find(movie => movie.id === activeMovieId2)?.image}
            alt={movies2.find(movie => movie.id === activeMovieId2)?.title}
          />
        </div>
      )}

      {/* Третий блок */}
      <div className="gridNew">
        {movies3.map(movie3 => (
          <div
            className="newBlok"
            key={movie3.id}
            onClick={() => handleMovieClick3(movie3.id)}
            style={{ cursor: "pointer", padding: "5px", margin: "0px" }}
          >
            <img
              src={movie3.image}
              alt={movie3.title}
              style={{
                maxWidth: "300px",
                maxHeight: "300px",
                border:
                  activeMovieId3 === movie3.id ? "2px solid blue" : "none",
              }}
            />
          </div>
        ))}
      </div>
      {activeMovieId3 && (
        <div>
          <h2>{movies3.find(movie => movie.id === activeMovieId3)?.title}</h2>
          <img className="minHeight"
            src={movies3.find(movie => movie.id === activeMovieId3)?.image}
            alt={movies3.find(movie => movie.id === activeMovieId3)?.title}
          />
        </div>
      )}

      {/* Четвертый блок */}
      <div className="gridNew ">
        {movies4.map(movie4 => (
          <div
            className="newBlok"
            key={movie4.id}
            onClick={() => handleMovieClick4(movie4.id)}
            style={{ cursor: "pointer", padding: "5px", margin: "0px" }}
          >
            <img
              src={movie4.image}
              alt={movie4.title}
              style={{
                maxWidth: "300px",
                maxHeight: "300px",
                border:
                  activeMovieId4 === movie4.id ? "2px solid blue" : "none",
              }}
            />
          </div>
        ))}
      </div>
      {activeMovieId4 && (
        <div className="displayNewFlex">
          <h2>{movies4.find(movie => movie.id === activeMovieId4)?.title}</h2>
          <img className="minHeight"
            src={movies4.find(movie => movie.id === activeMovieId4)?.image}
            alt={movies4.find(movie => movie.id === activeMovieId4)?.title}
          />
        </div>
      )}
      
    </div>
  )
}

export default Directions
