import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import MainIcon from "../images/main.jpg"
import Directions from "./Directions"
import About from "./About"

import Header from "./header"
import "../Style/layout.css"
import "../Style/bootstrap.css"
import "../Style/litle-style.css"

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <>
      {/* Меню сайта и навигация   */}
      <div>
        <div className="hero_area">
          <div className="hero_bg_box">
            <img src={MainIcon} alt=""></img>
          </div>
          <Header siteTitle={data.site.siteMetadata?.title || `Title`} />

          <section className="slider_section ">
            <div
              id="customCarousel1"
              className="carousel slide"
              data-ride="carousel"
            >
              <div className="carousel-inner">
                <div className="carousel-item active">
                  <div className="container ">
                    <div className="row">
                      <div className="col-lg-8 col-xl-6 mx-auto ">
                        <div className="iMac:mb-8 iMac:text-9xl font-serif font-bold text-white lg:text-7xl">
                          <h1 className="text">ALTYN JULDYZ</h1>
                          <p>
                            Группа компаний в Казахстане, вносящая огромный
                            вклад в развитие разных отраслей страны
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
        <hr />
      </div>

      {/*Наши проекты*/}
      <div>
        <div>
          <h1 className="newColorWhite newTextCenter">Направления Компании</h1>
        </div>
        <Directions />
      </div>
      <hr />

      {/* О на  */}
      <div>
        <h1 className="newColorWhite newTextCenter">О нас </h1>
        <About/>
      </div>

      {/* Наши партнеры */}
      <div>
        <h1 className="newColorWhite newTextCenter">Наши партнеры</h1>
      </div>

      {/* Больше о нашей компаний */}
      <div>
        <h1 className="newColorWhite newTextCenter">
          Узнавайте о нас больше на просторах интернета
        </h1>
      </div>
    </>
  )
}

export default Layout
