import React, { useState } from 'react';
import { Link } from "gatsby";
import MainIcon from '../images/logo-light.svg';

const Header = () => {
  const [isOpen, setOpen] = useState(false);

  const toggleMenu = () => {
    setOpen(!isOpen);
  }

  return (
    <header className="header_section">
      <div className="container">
        <nav className="navbar navbar-expand-lg custom_nav-container ">
          <Link to="/" className="navbar-brand">
            <img src={MainIcon} alt="" />
          </Link>
          <button className="navbar-toggler" onClick={toggleMenu} type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded={isOpen} aria-label="Toggle navigation">
            <span className=""> </span>
          </button>
          <div className={`collapse navbar-collapse ${isOpen ? "active" : ""}`} id="navbarSupportedContent">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link" to="/">Главная</Link>
              </li>
              <li className="nav-item">
                <div className="dropdown5">
                  <span className="dropbtn5">Направления
                    <i className="fa fa-caret-down"></i>
                  </span>
                  <div className="dropdown-content5">
                    <ul className="menu">
                      <li><a href="#">Производство и Торговля</a>
                        <ul>
                          <li><a href="#Fabrica">Швейно-обувная Фабрика</a></li>
                          <li><a href="#Protection">Средства Индивидуальной Защиты</a></li>
                          <li><a href="#Tools">Строительные Инструменты и Материалы</a></li>
                          <li><a href="#Equipment">Строительное Оборудование</a></li>
                          <li><a href="#Sporting-goods">Спортивные Товары</a></li>
                        </ul>
                      </li>
                      <li><a href="#">Строительство и Проектирование</a>
                        <ul>
                          <li><a href="#COMFORT-JAGALAU">Жилищное Строительство</a></li>
                          <li><a href="#Rental-of-Premises">Аренда Складских Помещений Класса А и С</a></li>
                        </ul>
                      </li>
                      <li><a href="#">Сельское Хозяйство</a>
                        <ul>
                          <li><a href="#Angus">Разведение КРС "Абердин Ангус" (племенной)</a></li>
                          <li><a href="#apple-orchard">Интенсивный Яблочный Сад</a></li>
                          <li><a href="#horse-breeding">Коневодство Органическое Удобрение - Биогумус (Вермикомпост)</a></li>
                          <li><a href="#Fish">Рыбоводство</a></li>
                          <li><a href="#premiumlamb">Разведение МРС "Романовские Овцы"</a></li>
                        </ul>
                      </li>
                    </ul>
                    <a href="#Car">Аренда VIP Авто</a>
                    <a href="#muvi">Кинематография</a>
                  </div>
                </div>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="#About">О компании</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="#contacts">Контакты</Link>
              </li>
            </ul>
          </div>
        </nav>
      </div>
    </header>
  );
}

export default Header;
